<template>
   <div class="recommend" ref="recommend">
    <scroll ref="scroll" class="recommend-content" :data="discList">
      <div>
        <!-- <div v-if="recommends.length" class="slider-wrapper">
          <div class="slider-content">
            <slider ref="slider">
              <div v-for="item in recommends">
                <a :href="item.linkUrl">
                  <img @load="loadImage" :src="item.picUrl">
                </a>
              </div>
            </slider>
          </div>
        </div> -->
        <div class="recommend-list">
          <h1 class="list-title">Popular Song List</h1>
          <!--<ul>
            <li @click="selectItem(item)" v-for="item in discList" class="item">
              <div class="icon">
                <img width="60" height="60" v-lazy="item.imgurl">
              </div>
              <div class="text">
                <h2 class="name" v-html="item.creator.name"></h2>
                <p class="desc" v-html="item.dissname"></p>
              </div>
            </li>
          </ul>-->

          <ul>
            <li @click="selectNewItem(item)" v-for="item in newRecommend" class="item">
              <div class="icon">
                <img width="60" height="60" v-lazy="'https://y.gtimg.cn/music/photo_new/T002R300x300M000' + item.album.mid + '.jpg'">
              </div>
              <div class="text">
                <h2 class="name" v-html="item.album.name"></h2>
                <p class="desc" v-html="item.singer[0]['name']"></p>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="loading-container" v-show="!discList.length">
        <loading></loading>
      </div>
    </scroll>
    <router-view></router-view>
  </div>
</template>

<script type="text/ecmascript-6">
  import Slider from 'base/slider/slider'
  import Loading from 'base/loading/loading'
  import Scroll from 'base/scroll/scroll'
  import {getRecommend, getDiscList, getNewRecommend} from 'api/recommend'
  import {getSongsUrl} from 'api/song'
  import {playlistMixin} from 'common/js/mixin'
  import {ERR_OK} from 'api/config'
  import {mapMutations, mapActions} from 'vuex'
  import AdsBox from '@/components/ads/ads'
  import { createSong, isValidMusic, processSongsUrl } from 'common/js/song'


  export default {
    mixins: [playlistMixin],
    data() {
      return {
        recommends: [],
        discList: [],
        newRecommend: []
      }
    },
    created() {
      //this._getRecommend()

      //this._getDiscList()
      this._getNewRecommend()
    },
    activated() {
      setTimeout(() => {
        this.$refs.slider && this.$refs.slider.refresh()
      }, 20)
    },
    methods: {
      handlePlaylist(playlist) {
        const bottom = playlist.length > 0 ? '60px' : ''

        this.$refs.recommend.style.bottom = bottom
        this.$refs.scroll.refresh()
      },
      loadImage() {
        if (!this.checkloaded) {
          this.checkloaded = true
          setTimeout(() => {
            this.$refs.scroll.refresh()
          }, 20)
        }
      },
      selectItem(item) {
        this.$router.push({
          path: `/recommend/${item.dissid}`
        })
        this.setDisc(item)
      },
      selectNewItem (item) {
        let key = []
        let index = 0
        key[0] = item
        getSongsUrl(key).then(res => {
          let data  = {
            albumname: item.album.name,
            interval: item.interval,
            filename: res.url_mid.data.midurlinfo[0]['filename'],
            image: 'https://y.gtimg.cn/music/photo_new/T002R300x300M000' + item.album.mid + '.jpg',
            songid: item.album.id,
            songmid: res.url_mid.data.midurlinfo[0]['songmid'],
            songname: item.album.name,
            singer: item.singer,
            albummid: item.album.mid,
            url: 'http://dl.stream.qqmusic.qq.com/' + res.url_mid.data.midurlinfo[0]['purl']
          }
          let song = []
          song.push(createSong(data))
          this.selectPlay({
            list: song,
            index
          })
        })
      },
      _getRecommend() {
        getRecommend().then((res) => {
          if (res.code === ERR_OK) {
            this.recommends = res.data.slider
          }
        })
      },
      _getDiscList() {
        getDiscList().then((res) => {
          if (res.code === ERR_OK) {
            this.discList = res.data.list
          }
        })
      },
      _getNewRecommend () {
        getNewRecommend().then(res => {
          this.newRecommend = res.new_song.data.song_list
        })
      },
      ...mapMutations({
        setDisc: 'SET_DISC'
      }),
      ...mapActions([
        'selectPlay',
        'randomPlay'
      ])
    },
    components: {
      Slider,
      Loading,
      Scroll,
	  AdsBox
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .recommend
    position: fixed
    width: 100%
    top: 88px
    bottom: 0
    .recommend-content
      height: 100%
      overflow: hidden
      .slider-wrapper
        position: relative
        width: 100%
        height: 0
        padding-top: 40%
        overflow: hidden
        .slider-content
          position: absolute
          top: 0
          left: 0
          width: 100%
          height: 100%
      .recommend-list
        .list-title
          height: 65px
          line-height: 65px
          text-align: center
          font-size: $font-size-medium
          color: $color-theme
        .item
          display: flex
          box-sizing: border-box
          align-items: center
          padding: 0 20px 20px 20px
          .icon
            flex: 0 0 60px
            width: 60px
            padding-right: 20px
          .text
            display: flex
            flex-direction: column
            justify-content: center
            flex: 1
            line-height: 20px
            overflow: hidden
            font-size: $font-size-medium
            .name
              margin-bottom: 10px
              color: $color-text
            .desc
              color: $color-text-d
      .loading-container
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)
</style>



